import React, { Component } from 'react';
import { Row, Col, Divider } from 'antd';
import { TableData } from 'components/common';
import { BtnDelete, BtnAddNew, Btn } from 'components/common/button';
import {
  getStudentAdditionSubject,
  deleteRegisterStudentCreditEnrollment,
} from 'services/additionalRegistration';
import { changeApprovedByCCEId } from 'services/classCreditEnrollment';

class TableAddRegisterSelected extends Component {
  constructor(props) {
    super(props);
    this.state = {
      totalCredits: 0,
      totalSubjects: 0,
      selectedRowKeys: [],
      selectedRows: [],
      removing: false,
      adding: false,
    };
  }

  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys, selectedRows });
  };

  getStudentAdditionSubject = async paramSearch => {
    const res = await getStudentAdditionSubject(paramSearch);
    const { items } = res.data;
    const totalCredits = items.map(item => item.noOfCredit).reduce((a, b) => a + b, 0);
    const totalSubjects = items.length;
    this.setState({ totalCredits, totalSubjects });
    return res;
  };

  approvalRegister = async record => {
    const { doReload } = this.props;
    const dataChange = {
      cceId: record.cceId,
      isApproved: !record.isApproved,
    };
    try {
      await changeApprovedByCCEId(dataChange);
      doReload();
    } catch (err) {
      console.error(err);
    }
  };

  columns = [
    {
      title: 'M hc phn',
      dataIndex: 'syllableCode',
      key: 'syllableCode',
    },
    {
      title: 'Tn hc phn',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'S TC',
      dataIndex: 'noOfCredit',
      key: 'noOfCredit',
    },
    {
      title: 'Tn lp tn ch',
      dataIndex: 'classCreditCode',
      key: 'classCreditCode',
    },
    {
      title: 'Lch hc',
      dataIndex: 'schedule',
      key: 'schedule',
    },
    {
      title: 'Gio vin',
      dataIndex: 'teacherName',
      key: 'teacherName',
    },
    {
      title: 'Phng hc',
      dataIndex: 'roomName',
      key: 'roomName',
    },
    {
      title: 'Cn trng',
      dataIndex: 'noOfEmptySlot',
      key: 'noOfEmptySlot',
    },
    {
      title: 'Duyt ng k',
      dataIndex: 'noOfEmptySlot2',
      key: 'noOfEmptySlot2',
      render: (_text, record) => {
        return (
          <div>
            <Btn onClick={() => this.approvalRegister(record)}>
              {record.isApproved ? 'Hy Duyt' : 'Duyt'}
            </Btn>
          </div>
        );
      },
    },
  ];

  handleDelete = async () => {
    this.setState({ removing: true });
    const { selectedRows } = this.state;
    const { student, doReload } = this.props;
    const deleteCCEs = selectedRows.map(selectedRow => ({
      ccsId: selectedRow.ccsId,
      studentId: student.id,
    }));
    const reqBody = {
      userLogged: '3fa85f64-5717-4562-b3fc-2c963f66afa6',
      deleteCCEs,
    };
    try {
      await deleteRegisterStudentCreditEnrollment(reqBody);
      this.setState({ selectedRowKeys: [], selectedRows: [] });
      doReload();
    } catch (err) {
      console.error(err);
    }
    this.setState({ removing: false });
  };

  handleAddSubject = async () => {
    this.setState({ adding: true });
    const { handleAddSubject } = this.props;
    await handleAddSubject();
    this.setState({ adding: false });
  };

  render() {
    const {
      totalCredits,
      totalSubjects,
      selectedRows,
      selectedRowKeys,
      removing,
      adding,
    } = this.state;
    const {
      paramSearch,
      // student,
      reload,
      subjectSelectedRows,
    } = this.props;
    paramSearch.isRegister = true;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <br />
        <Row gutter={24}>
          <Col span={6}>
            S hc phn: <b>{totalSubjects}</b>
          </Col>
          <Col span={6}>
            S TC: <b>{totalCredits}</b>
          </Col>
          <Col span={6}>
            S tin phi np: <b>0</b>
          </Col>
        </Row>
        <br />
        <Divider>Danh sch mn  ng k</Divider>
        <BtnAddNew
          onClick={this.handleAddSubject}
          title="Thm"
          disabled={subjectSelectedRows.length === 0}
          loading={adding}
          style={{ marginRight: '15px' }}
        />
        <BtnDelete
          onClick={this.handleDelete}
          title="Xa"
          disabled={selectedRows.length === 0}
          loading={removing}
        />
        <TableData
          reload={reload}
          rowSelection={rowSelection}
          service={this.getStudentAdditionSubject}
          paramSearch={paramSearch}
          columns={this.columns}
          allowGetData={
            !!paramSearch.studentCode &&
            // !!paramSearch.studyFieldId &&
            !!paramSearch.yearEduId &&
            !!paramSearch.semesterId &&
            !!paramSearch.registerSessionId
          }
          rowKey="syllableCode"
        />
      </div>
    );
  }
}

export default TableAddRegisterSelected;



