import { request } from 'utils';

const prefix = '/api/education';

export const getDataForSelect = ({ trainTypeId, yearEduId, semesterId }) =>
  request({
    prefix,
    url: `/RegisterSession/DataForSelect`,
    params: {
      trainTypeId,
      yearEduId,
      semesterId,
      fullFormat: true,
    },
    method: 'GET',
  });

export const getRegisterConfig = ({
  trainTypeId,
  yearEduId,
  facultyId,
  semesterId,
  registerSessionId,
  courseId,
  studyFieldId,
  classId,
}) =>
  request({
    prefix,
    url: `/RegisterConfig`,
    params: {
      trainTypeId,
      yearEduId,
      facultyId,
      semesterId,
      registerSessionId,
      courseId,
      studyFieldId,
      classId,
    },
    method: 'GET',
  });

export const getRegisterConfigById = id =>
  request({
    prefix,
    url: `/RegisterConfig/${id}`,
    method: 'GET',
  });

export const addRegisterConfig = dataAdd =>
  request({
    prefix,
    url: `/RegisterConfig`,
    data: {
      ...dataAdd,
    },
    method: 'POST',
  });

export const editRegisterConfig = (id, dataEdit) =>
  request({
    prefix,
    url: `/RegisterConfig/${id}`,
    data: {
      ...dataEdit,
    },
    method: 'PUT',
  });

export const activateRegisterConfig = id =>
  request({
    prefix,
    url: `/RegisterConfig/${id}/Activate`,
    data: {
      id,
      isOnline: true,
    },
    method: 'PUT',
  });

export const deleteRegisterConfig = id =>
  request({
    prefix,
    url: `/RegisterConfig/${id}`,
    method: 'DELETE',
  });



