import { request } from 'utils';

const prefix = '/api/education';
const prefixStudent = '/api/students';

export const getStudentAdditionSubject = ({
  studentCode,
  facultyId,
  studyFieldId,
  yearEduId,
  semesterId,
  registerSessionId,
  isRegister,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/GetStudentAdditionSubject',
    params: {
      studentCode,
      facultyId,
      studyFieldId,
      yearEduId,
      semesterId,
      registerSessionId,
      isRegister,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    method: 'GET',
  });

export const getStudentByCode = studentCode =>
  request({
    prefix: prefixStudent,
    url: `/Student/GetByCode/${studentCode}`,
    method: 'GET',
  });

export const deleteRegisterStudentCreditEnrollment = reqBody =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/DeleteRegisterStudentCreditEnrollment',
    method: 'DELETE',
    data: reqBody,
  });

export const createRegisterStudentCreditEnrollment = reqBody =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/CreateRegisterStudentCreditEnrollment',
    method: 'POST',
    data: reqBody,
  });



