import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Spin } from 'antd';
import { getQueryParams } from 'utils/urlUtil';
import { getStudentByCode } from 'services/additionalRegistration';
import FormInfoCommon from './FormInfoCommon';
import TableAddRegisterNotSelected from './TableAddRegisterNotSelected';
import TableAddRegisterSelected from './TableAddRegisterSelected';

class AdditionalRegistrationPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      paramSearch: {},
      student: {},
      loading: false,
      reload: 0,
      subjectSelectedRows: [],
    };
    this.addSubjectRef = React.createRef();
  }

  componentDidMount() {
    this.updateQueryParam();
  }

  componentWillReceiveProps() {
    this.updateQueryParam();
  }

  setSubjectSelectedRows = subjectSelectedRows => {
    this.setState({ subjectSelectedRows });
  };

  updateQueryParam = async () => {
    this.setState({ loading: true });
    try {
      const queryParams = getQueryParams();
      if (queryParams.studentCode) {
        const res = await getStudentByCode(queryParams.studentCode);
        this.setState({ student: res.data });
      }
      this.setState({ paramSearch: queryParams });
    } finally {
      this.setState({ loading: false });
    }
  };

  doReload = () => {
    const { reload } = this.state;
    this.setState({ reload: reload + 1 });
  };

  handleAddSubject = async () => {
    await this.addSubjectRef.current.handleAdd();
  };

  render() {
    const { paramSearch, student, loading, reload, subjectSelectedRows } = this.state;

    return (
      <Card>
        <Spin spinning={loading}>
          <Row>
            <Col span={24}>
              <FormInfoCommon />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableAddRegisterNotSelected
                paramSearch={paramSearch}
                student={student}
                reload={reload}
                doReload={this.doReload}
                setSubjectSelectedRows={this.setSubjectSelectedRows}
                ref={this.addSubjectRef}
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <TableAddRegisterSelected
                paramSearch={paramSearch}
                student={student}
                reload={reload}
                doReload={this.doReload}
                handleAddSubject={this.handleAddSubject}
                subjectSelectedRows={subjectSelectedRows}
              />
            </Col>
          </Row>
        </Spin>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AdditionalRegistrationPage />
  </Page>
);



